<template>
    <RouterLink
      :to="to"
      :type="type"
      class="btn"
      :class="[
        `btn-${color}`,
        sizeClass,
        { 'd-block w-100': block }
      ]"
      :style="customStyle"
    >
      <slot></slot>
    </RouterLink>
  </template>
  
  <script setup>
  import { computed } from 'vue';
  
  const props = defineProps({
    type: {
      type: String,
      default: 'button',
    },
    color: {
      type: String,
      default: 'primary', // Warna default (sesuai class Bootstrap)
    },
    size: {
      type: String,
      default: 'md', // 'sm', 'md', 'lg'
      validator: (value) => ['sm', 'md', 'lg'].includes(value)
    },
    block: {
      type: Boolean,
      default: false, // Jika true, button memenuhi lebar parent
    },
    customStyle: {
      type: Object,
      default: () => ({}),
    },
    padding: {
      type: String,
      default: '', // Jika ingin custom padding
    },
  });
  
  // Mapping ukuran ke class Bootstrap
  const sizeClass = computed(() => {
    if (props.size === 'sm') return 'btn-sm';
    if (props.size === 'lg') return 'btn-lg';
    return ''; // Default (medium)
  });
  </script>
  
  <style scoped>
  /*styling tambahan */
  .btn {
    transition: all 0.3s ease;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    background-color: #dc3545;
  }
  .btn:hover {
    background-color: #b61626;
    transform: translateY(-1px);
}

  /* Ukuran custom */
.btn-sm { min-width: 80px; height: 32px; font-size: 12px; }
.btn-md { min-width: 120px; height: 40px; font-size: 14px; }
.btn-lg { min-width: 160px; height: 48px; font-size: 16px; }
  </style>